import React from "react";

class ControlOption extends React.Component {
	render() {
		return (
			<div className="form-check form-check-inline hand">
				<input
					onClick={this.props.clickEvent}
					className="form-check-input hand"
					type={this.props.inputType}
					id={this.props.name}
					checked={this.props.isChecked}
					onChange={() => {}}
				></input>
				<label htmlFor={this.props.name} className="my-auto hand">
					{this.props.name.replace(/-/g, " ")}
				</label>
			</div>
		);
	}
}

export default ControlOption;
