import React from "react";
import ControlOption from "./ControlOption.js";
import ControlDate from "./ControlDate.js";

class Control extends React.Component {
	constructor(props) {
		super(props);
		this.state = props.currentSettings;
	}

	render() {
		return (
			<div id="selectData" className="modal fade" data-backdrop="static" data-keyboard="false">
				<div className="modal-dialog modal-lg">
					<div className="modal-content">
						<div className="modal-header">
							<h1 className="modal-title">Seleziona i dati da vedere:</h1>
						</div>
						<div className="modal-body">
							<form>
								<div className="form-group inline">
									<p>Che dato vuoi vedere?</p>
									<ControlOption
										name="Valori-giornalieri"
										isChecked={this.state.graphData == "STORICO"}
										clickEvent={() => {
											this.setState({ graphData: "STORICO" });
										}}
										inputType="radio"
									/>
									<ControlOption
										name="Variazione-giornaliera"
										isChecked={this.state.graphData == "VARIAZIONE"}
										clickEvent={() => {
											this.setState({ graphData: "VARIAZIONE" });
										}}
										inputType="radio"
									/>
								</div>
								<hr />
								<div className="form-group inline">
									<p>Come lo vuoi vedere?</p>
									<ControlOption
										name="Come-dato-numerico"
										isChecked={!this.state.percentage}
										clickEvent={() => {
											this.setState({ percentage: false });
										}}
										inputType="radio"
									/>
									<ControlOption
										name="Come-percentuale"
										isChecked={this.state.percentage}
										clickEvent={() => {
											this.setState({ percentage: true });
										}}
										inputType="radio"
									/>
								</div>
								<hr />
								<div className="form-group inline">
									<p>
										Seleziona i campi che vuoi vedere (meno ne metti, più la risposta sarà rapida):
									</p>
									<div>
										<p>
											<u
												className="hand"
												onClick={() => {
													this.setState({
														graphList: this.props.fieldsList.map((item) => {
															return item.db;
														})
													});
												}}
											>
												Seleziona tutti
											</u>
											{" - "}
											<u
												className="hand"
												onClick={() => {
													this.setState({ graphList: [] });
												}}
											>
												Deseleziona tutti
											</u>
										</p>
									</div>
									{this.props.fieldsList.map((field) => {
										return (
											<ControlOption
												key={field.name}
												name={field.name}
												isChecked={this.state.graphList.indexOf(field.db) != -1}
												inputType="checkbox"
												clickEvent={() => {
													const index = this.state.graphList.indexOf(field.db);
													let graphList = this.state.graphList;
													if (index != -1) graphList.splice(index, 1);
													else graphList.push(field.db);
													this.setState({ graphList: graphList });
												}}
											/>
										);
									})}
								</div>
								<hr />
								<div className="form-group">
									<div className="row">
										<ControlDate
											what="da"
											date={this.state.startDate}
											onUpdate={(event) => {
												this.setState({ startDate: event.target.value });
											}}
										/>
										<ControlDate
											what="a"
											date={this.state.endDate}
											onUpdate={(event) => {
												this.setState({ endDate: event.target.value });
											}}
										/>
									</div>
									<p
										className="hand mt-2"
										onClick={() => {
											const date = new Date(Date.now())
											this.setState({ startDate: "2020-02-24", endDate: date.toISOString().substr(0,10) })
											//setTimeout(this.forceUpdate(), 100);
										}}
									>
										<u>Visualizza da inzio pandemia a oggi</u>
									</p>
								</div>
							</form>
						</div>
						<div className="modal-footer">
							<button
								className="btn btn-secondary"
								type="button"
								data-dismiss="modal"
								onClick={() => {
									this.props.alterSettings(this.state);
								}}
							>
								Conferma
							</button>
							<button
								className="btn btn-secondary"
								type="button"
								data-dismiss="modal"
								onClick={() => {
									setTimeout(() => {
										this.setState(this.props.currentSettings);
									}, 500);
								}}
							>
								Annulla
							</button>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Control;
